@import url(//netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.css);
@import url(https://use.fontawesome.com/releases/v5.7.2/css/all.css);
@import url(https://fonts.googleapis.com/css?family=Titillium+Web:300);
* {
  margin: 0px;
  font-family: "Georgia";
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

.fa-2x {
  font-size: 3em;
}
.fa {
  position: relative;
  display: table-cell;
  width: 60px;
  height: 36px;
  text-align: center;
  vertical-align: middle;
  font-size: 36px;
}

.main-menu:hover,
nav.main-menu.expanded {
  width: 250px;
  overflow: visible;
}

.menubar-brand {
  font-size: 46px;
  color: #212121;
}

@media (max-width: 992px) {
  .main-menu {
    position: absolute;
  }
}

@media (min-width: 992px) {
  .main-menu {
    position: fixed;
  }
}

.main-menu {
  background: #212121;
  border-right: 1px solid #e5e5e5;
  top: 0;
  bottom: 0;
  height: 100vh;
  left: 0;
  width: 60px;
  overflow: hidden;
  transition: width 0.05s linear;
  -webkit-transform: translateZ(0) scale(1, 1);
  z-index: 1000;
}

.main-menu > ul {
  margin: 0;
}

.side-brand {
  background-color: whitesmoke;
  margin-bottom: 16px;
}
.main-menu li {
  position: relative;
  display: block;
  width: 250px;
}

.main-menu li > a {
  margin: 0px 0 34px 0;
  position: relative;
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
  color: #999;
  font-size: 22px;
  text-decoration: none;
  -webkit-transform: translateZ(0) scale(1, 1);
  transition: all 0.1s linear;
}

.main-menu .nav-icon {
  position: relative;
  display: table-cell;
  width: 60px;
  height: 36px;
  text-align: center;
  vertical-align: middle;
  font-size: 18px;
}

.main-menu .nav-text {
  text-transform: uppercase;
  position: relative;
  display: table-cell;
  vertical-align: middle;
  width: 190px;
}

.main-menu > ul.logout {
  position: absolute;
  left: 0;
  bottom: 0;
}

.no-touch .scrollable.hover {
  overflow-y: hidden;
}

.no-touch .scrollable.hover:hover {
  overflow-y: auto;
  overflow: visible;
}

a:hover,
a:focus {
  text-decoration: none;
}

nav {
  -webkit-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

nav ul,
nav li {
  outline: 0;
  margin: 0;
  padding: 0;
}
.main-menu li:hover > a,
nav.main-menu li.active > a,
.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus,
.dropdown-menu > .active > a,
.dropdown-menu > .active > a:hover,
.dropdown-menu > .active > a:focus,
.no-touch .dashboard-page nav.dashboard-menu ul li:hover a,
.dashboard-page nav.dashboard-menu ul li.active a {
  color: #fff;
  background-color: #5fa2db;
}

@media (min-width: 992px) {
  .main-content {
    margin-left: 60px;
  }
}

.content-links {
  display: flex;
  justify-content: space-evenly;
}

.carousel-control-next,
.carousel-control-prev {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.content-links a:hover {
  color: white;
}
.caro-slide-content > .content > h2 {
  margin-bottom: 26px;
}
.caro-slide-content > .content {
  background-color: rgb(189, 189, 189);
}
@media (min-width: 992px) {
  .carousel,
  .carousel-inner,
  .carousel-item {
    width: 100%;
    height: 55vh;
  }
  .content-img img {
    width: 100%;
    height: 55vh;
  }

  .caro-slide-content {
    display: flex;
  }
  .caro-slide-content .content,
  .content-img {
    width: 50%;
    height: 55vh;
  }
  .content {
    padding: 32px 16px;
  }
}

@media (max-width: 992px) {
  .carousel {
    margin-left: 60px;
  }
  .carousel,
  .carousel-inner,
  .carousel-item {
    height: 100vh;
  }
  .content-img img {
    width: 100%;
    height: 45vh;
  }
  .caro-slide-content .content,
  .content-img {
    width: 100%;
    height: 55vh;
  }
  .content {
    padding: 26px 8px;
  }
}

.topics-list .row {
  display: inline-flex;
  width: 100%;
  margin: 0px;
}

.categories h2 {
  color: black;
  text-align: center;
}
.topics-list a {
  color: black;
}
.topics-list .column {
  text-align: center;
  margin-bottom: 64px;
  padding: 4px 8px;
  border-bottom: 3px solid black;
  border-top: 3px solid black;
  border-radius: 26px;
}
.topics-list .column:hover a {
  color: white;
}
.topics-list .column:hover {
  background-color: black;
}
@media (max-width: 992px) {
  .categories h2 {
    font-size: 36px;
    margin: 18px 0;
  }
  .topics-list a {
    font-size: 18px;
  }
}

@media (min-width: 992px) {
  .categories h2 {
    font-size: 56px;
    margin: 36px 0;
  }
  .topics-list {
    padding: 12px 16px;
  }
  .topics-list a {
    font-size: 20px;
  }
}

@media (min-width: 992px) {
  .article {
    width: 800px;
    margin-bottom: 64px;
  }
  .article h3 {
    color: black;
    font-size: 28px;
  }
  .articles > h2 {
    font-size: 62px;
    margin: 32px 0px;
  }
}

@media (max-width: 992px) {
  .article {
    margin-bottom: 42px;
  }
  .articles > h2 {
    font-size: 34px;
    margin: 26px 0px;
  }
  .article h3 {
    color: black;
    font-size: 24px;
  }
}

.articles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.articles h2 {
  text-transform: uppercase;
  text-align: center;
}
.article p {
  color: black;
  font-size: 16px;
  padding: 0px;
  margin: 0px;
}
.article a {
  color: white;
  padding: 2px 6px;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.562);
}
.article a:hover {
  color: whitesmoke;
  background-color: black;
}

.solo-article-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.article-detail h4 {
  text-align: center;
}
.article-detail img {
  width: 100%;
}

@media (min-width: 992px) {
  .solo-article-div .article-detail {
    width: 800px;
    max-width: 800px;
  }
  .solo-article-div {
    margin: 36px 0px;
    padding: 24px 0px;
  }
  .article-detail h4 {
    font-size: 42px;
  }
  .article-detail .article-link {
    display: flex;
    justify-content: space-evenly;
    background-color: rgba(0, 0, 0, 0.603);
  }
  .article-detail .article-link a {
    color: rgb(0, 0, 0);
    padding: 0px;
    margin-bottom: 0;
    background-color: white;
  }
  .article-detail .article-link a:hover {
    color: white;
    padding: 0px;
    margin-bottom: 0;
    background-color: rgb(0, 0, 0);
  }
  .article-detail .article-link p {
    color: rgb(255, 255, 255);
    padding: 0px;
    margin-bottom: 0;
  }
}
.article-detail article {
  padding: 0 4px;
}
@media (max-width: 992px) {
  .solo-article-div {
    margin: 36px 0px;
    padding: 24px 0px;
  }
  .article-detail h4 {
    font-size: 22px;
  }
  .article-detail .article-link a {
    padding: 0px;
    margin-bottom: 12px;
  }
  .article-detail .article-link p {
    padding: 0px;
    margin-bottom: 12px;
  }
}

